<template>
    <div class="search">
        <search-header @toSearch="toSearch"></search-header>
        <keep-alive>
            <router-view @toSearch="toSearch"/>
        </keep-alive>
    </div>
</template>

<script>
    import searchHeader from '@/components/search/searchHeader'
    import {mapState} from 'vuex'

    export default {
        name: "search",
        components: {
            searchHeader
        },
        data() {
            return {
                showResult: false
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword(value) {
                if (!value.trim().length) {
                    this.$router.replace('/search');
                }
            }
        },
        methods: {
            toSearch() {
                if (this.$route.path === '/search') {
                    this.$router.replace('/search/result/single');
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .search {
        padding-top: 15px;
    }
</style>



