<template>
    <div class="single">
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="index">
                <div class="main">
                    <div class="title">
                        <p>
                            <span>{{item.name}}</span>
                            <i class="iconfont icon-bofangmv mv_icon" v-show="item.mvid"></i>
                        </p>
                        <p class="alias" v-if="item.alias.length">( {{item.alias[0]}} )</p>
                    </div>
                </div>
                <span class="artists">
                    <span v-for="artist,index in item.artists" :key="index">{{artist.name}}</span>
                </span>
                <span class="album">{{item.album.name}}</span>
                <span class="duration">{{item.duration | formatSeconds}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "single",
        data() {
            return {
                listData: []
            }
        },
        filters: {
            formatSeconds(a) {
                a = a / 1000;
                let hh = parseInt(a / 3600);
                if (hh < 10) hh = "0" + hh;
                let mm = parseInt((a - hh * 3600) / 60);
                if (mm < 10) mm = "0" + mm;
                let ss = parseInt((a - hh * 3600) % 60);
                if (ss < 10) ss = "0" + ss;
                let length = hh !== '00' ? hh + ":" + mm + ":" + ss : mm + ":" + ss;
                if (a > 0) {
                    return length;
                } else {
                    return "NaN";
                }
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    keywords: this.$store.state.searchKeyword
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.songs || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .single {
        ul {
            li {
                padding: 12px 15px;
                display: flex;
                line-height: 18px;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                .main {
                    width: 48%;
                    display: flex;
                    align-items: center;
                    .title {
                        p {
                            .mv_icon {
                                vertical-align: -4px;
                                padding-left: 5px;
                                color: #b7181a;
                                font-size: 30px;
                                text-shadow: 0 0 0 #b7181a;
                            }
                        }
                        .alias {
                            padding-top: 10px;
                            color: #999;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }
                    }
                }
                .artists, .album, .duration {
                    color: #999;
                    font-size: 12px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .artists {
                    width: 22%;
                    padding-right: 20px;
                    span {
                        padding-right: 10px;
                    }
                }
                .album {
                    width: 24%;
                    padding-right: 20px;
                }
                .duration {
                    width: 6%;
                    text-align: right;
                }
            }
        }
    }
</style>



