<template>
    <div class="user">
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="index">
                <div class="cover">
                    <img :src="item.avatarUrl" alt="">
                    <span class="title">{{item.nickname}}</span>
                    <span class="gender" v-if="item.gender">
                        <i class="iconfont icon-nan" v-if="item.gender===1"></i>
                        <i class="iconfont icon-nv" v-if="item.gender===2"></i>
                    </span>
                </div>
                <div class="signature" v-if="item.signature.length">{{item.signature}}</div>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "user",
        data() {
            return {
                listData: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            },
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    keywords: this.keyword,
                    type: 1002
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.userprofiles || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .user {
        ul {
            li {
                padding: 10px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                .cover {
                    display: flex;
                    align-items: center;
                    img {
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                    }
                    .title {
                        padding-left: 10px;
                    }
                    .gender {
                        i {
                            padding-left: 5px;
                            font-size: 12px;
                        }
                        .icon-nan {
                            color: dodgerblue;
                            text-shadow: 0 0 0 dodgerblue;
                        }
                        .icon-nv {
                            color: hotpink;
                            text-shadow: 0 0 0 hotpink;
                        }
                    }
                }
                .signature {
                    position: relative;
                    padding: 10px;
                    background-color: #fff;
                    border: 1px solid #ddd;
                    border-radius: 6px;
                    color: #999;
                    font-size: 12px;
                    &:after {
                        content: '<';
                        position: absolute;
                        top: 9px;
                        left: -11px;
                        width: 10px;
                        height: 10px;
                        line-height: 10px;
                        color: #ddd;
                        font-size: 16px;
                        border-right: 1px solid #fff;
                    }
                }
            }
        }
    }
</style>



