<template>
    <div class="new_music">
        <header>
            <i class="iconfont icon-bofang" style="color: #b7181a;"></i>
            <span></span>
        </header>
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="item.id">
                <div class="main">
                    <span class="index">{{index+1 | zeroizeIndex}}</span>
                    <i :class="`iconfont ${isCollected(item.id)?'icon-xihuan1':'icon-xihuan'}`" @click="switchFavorites(item)"></i>
                    <img :src="item.album.picUrl" alt="">
                    <span class="title">
                        <span>{{item.name}}</span>
                        <span class="alias" v-if="item.alias.length">( {{item.alias[0]}} )</span>
                    </span>
                    <i class="iconfont icon-bofangmv mv_icon" v-show="item.mvid"></i>
                </div>
                <span class="artists">
                    <span v-for="artist,index in item.artists" :key="index">{{artist.name}}</span>
                </span>
                <span class="album">{{item.album.name}}</span>
                <span class="duration">{{item.duration | formatSeconds}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex'

    export default {
        name: "newMusic",
        data() {
            return {
                listData: []
            }
        },
        computed: mapState([
            'favorites'
        ]),
        filters: {
            zeroizeIndex(index) {
                return index < 10 ? '0' + index : index;
            },
            formatSeconds(a) {
                a = a / 1000;
                let hh = parseInt(a / 3600);
                if (hh < 10) hh = "0" + hh;
                let mm = parseInt((a - hh * 3600) / 60);
                if (mm < 10) mm = "0" + mm;
                let ss = parseInt((a - hh * 3600) % 60);
                if (ss < 10) ss = "0" + ss;
                let length = hh !== '00' ? hh + ":" + mm + ":" + ss : mm + ":" + ss;
                if (a > 0) {
                    return length;
                } else {
                    return "NaN";
                }
            }
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let {code, data} = await this.$axios.get('http://www.ruyiday.com/top/song');
                if (code === 200) {
                    this.listData = data;
                }
            },
            isCollected(id) {
                for (let i = 0; i < this.favorites.length; i++) {
                    if (this.favorites[i].id === id) {
                        return true;
                    }
                }
                return false;
            },
            ...mapMutations([
                'switchFavorites'
            ]),
        }
    }
</script>

<style lang="scss" scoped>
    .new_music {
        ul {
            padding: 20px 0;
            li {
                padding: 6px 10px;
                display: flex;
                align-items: center;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                .main {
                    width: 56%;
                    display: flex;
                    align-items: center;
                    .index {
                        width: 36px;
                        text-align: center;
                        font-size: 12px;
                    }
                    i {
                        color: #999;
                        font-size: 14px;
                        &.icon-xihuan1 {
                            color: #b7181a;
                        }
                    }
                    img {
                        width: 50px;
                        height: 50px;
                        margin: 0 12px;
                        border: 1px solid #eee;
                    }
                    .title {
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        .alias {
                            color: #999;
                        }
                    }
                    .mv_icon {
                        padding-left: 5px;
                        padding-bottom: 4px;
                        color: #b7181a;
                        font-size: 30px;
                        text-shadow: 0 0 0 #b7181a;
                    }
                }
                .artists, .album, .duration {
                    color: #999;
                    font-size: 12px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .artists {
                    width: 20%;
                    padding-right: 20px;
                    span {
                        padding-right: 10px;
                    }
                }
                .album {
                    width: 20%;
                    padding-right: 20px;
                }
                .duration {
                    width: 4%;
                    text-align: right;
                }
            }
        }
    }
</style>



