<template>
    <div class="favorites">
        <div class="cont" v-show="favorites.length">
            <div class="option">
                <i class="iconfont icon-bofang" style="color: #b7181a;"></i>
                <span></span>
            </div>
            <ul>
                <li v-for="item,index in favorites" :key="item.id">
                    <div class="main">
                        <span class="index">{{index+1 | zeroizeIndex}}</span>
                        <i :class="`iconfont ${isCollected(item.id)?'icon-xihuan1':'icon-xihuan'}`" @click="switchFavorites(item)"></i>
                        <span class="title">
                            <span>{{item.name}}</span>
                            <span class="alias" v-if="item.alia&&item.alia.length">( {{item.alia[0]}} )</span>
                            <span class="alias" v-else-if="item.alias&&item.alias.length">( {{item.alia[0]}} )</span>
                        </span>
                        <i class="iconfont icon-bofangmv mv_icon" v-show="item.mv"></i>
                    </div>
                    <span class="artists" v-if="item.ar&&item.ar.length">
                        <span v-for="artist,index in item.ar" :key="index">{{artist.name}}</span>
                    </span>
                    <span class="artists" v-else-if="item.artists&&item.artists.length">
                        <span v-for="artist,index in item.artists" :key="index">{{artist.name}}</span>
                    </span>
                    <span class="album" v-if="item.al">{{item.al.name}}</span>
                    <span class="album" v-else-if="item.album">{{item.album.name}}</span>
                    <span class="duration" v-if="item.dt">{{item.dt | formatSeconds}}</span>
                    <span class="duration" v-else-if="item.duration">{{item.duration | formatSeconds}}</span>
                </li>
            </ul>
        </div>
        <p v-show="!favorites.length" style="padding: 50px 0;text-align: center;color: #999;font-size: 12px;"></p>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'
    import {mapState, mapMutations} from 'vuex'

    export default {
        name: "favorites",
        components: {
            columnHeader
        },
        data() {
            return {
                detail: {}
            }
        },
        computed: mapState([
            'favorites'
        ]),
        filters: {
            zeroizeIndex(num) {
                return num < 10 ? '0' + num : num;
            },
            formatSeconds(a) {
                a = a / 1000;
                let hh = parseInt(a / 3600);
                if (hh < 10) hh = "0" + hh;
                let mm = parseInt((a - hh * 3600) / 60);
                if (mm < 10) mm = "0" + mm;
                let ss = parseInt((a - hh * 3600) % 60);
                if (ss < 10) ss = "0" + ss;
                let length = hh !== '00' ? hh + ":" + mm + ":" + ss : mm + ":" + ss;
                if (a > 0) {
                    return length;
                } else {
                    return "NaN";
                }
            }
        },
        watch: {
            $route: 'getDetail'
        },
        mounted() {
            this.getDetail();
        },
        methods: {
            async getDetail() {
                let params = {
                    id: this.$route.query.id
                };
                let {code, playlist} = await this.$axios.get('http://www.ruyiday.com/playlist/detail', {params});
                if (code === 200) {
                    this.detail = playlist;
                }
            },
            ...mapMutations([
                'switchFavorites'
            ]),
            isCollected(id) {
                for (let i = 0; i < this.favorites.length; i++) {
                    if (this.favorites[i].id === id) {
                        return true;
                    }
                }
                return false;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .favorites {
        padding-top: 15px;
        .cont {
            .option {
                padding-bottom: 20px;
            }
            ul {
                li {
                    height: 46px;
                    padding: 6px 10px;
                    display: flex;
                    align-items: center;
                    &:nth-child(2n+1) {
                        background-color: #f8f8f8;
                    }
                    &:hover {
                        background-color: #f5f5f5;
                    }
                    .main {
                        width: 50%;
                        display: flex;
                        align-items: center;
                        .index {
                            padding-left: 10px;
                            text-align: center;
                            font-size: 12px;
                        }
                        i {
                            padding: 0 12px;
                            color: #999;
                            font-size: 14px;
                            &.icon-xihuan1 {
                                color: #b7181a;
                            }
                        }
                        .title {
                            height: 15px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            .alias {
                                color: #999;
                            }
                        }
                        .mv_icon {
                            padding-left: 5px;
                            padding-bottom: 4px;
                            color: #b7181a;
                            font-size: 30px;
                            text-shadow: 0 0 0 #b7181a;
                        }
                    }
                    .artists, .album, .duration {
                        color: #999;
                        font-size: 12px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                    .artists {
                        width: 22%;
                        padding-right: 20px;
                        span {
                            padding-right: 10px;
                        }
                    }
                    .album {
                        width: 20%;
                        padding-right: 20px;
                    }
                    .duration {
                        width: 8%;
                        text-align: right;
                    }
                }
            }
        }
    }
</style>



