<template>
    <div class="song_list">
        <column-header :icon="'icon-kongzhiqirili'" :title="''" :link="'/find/songList'"></column-header>
        <ul v-if="listData.length">
            <router-link tag="li" :to="{path: '/songListDetail', query: {id: item.id}}" v-for="item,index in listData" :key="item.id" v-if="index<10">
                <span class="play_count">
                    <i class="iconfont icon-erjiicon"></i>
                    <span>{{item.playCount | unitMyriad}}</span>
                </span>
                <img :src="item.picUrl" alt="">
                <p class="title">{{item.name}}</p>
            </router-link>
        </ul>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'

    export default {
        name: "songList",
        components: {
            columnHeader
        },
        data() {
            return {
                listData: []
            }
        },
        async mounted() {
            let res = await this.$axios.get('http://www.ruyiday.com/personalized');
            if (res.code === 200) {
                this.listData = res.result;
            }
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .song_list {
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li {
                width: calc((100% - 18px * 4) / 5);
                margin-bottom: 24px;
                position: relative;
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                .play_count {
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    padding: 1px 4px 0 0;
                    color: #fff;
                    font-size: 12px;
                    background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
                    i {
                        display: inline-block;
                        vertical-align: middle;
                        font-size: 12px;
                        transform: scale(0.8);
                    }
                }
                img {
                    display: block;
                    width: 100%;
                }
                .title {
                    padding-top: 10px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
    }
</style>



