import find from '@/pages/find/index'
import recommend from '@/pages/find/recommend'
import songList from '@/pages/find/songList'
import newMusic from '@/pages/find/newMusic'
import singer from '@/pages/find/singer'

export default {
    path: '/find',
    component: find,
    redirect: '/find/recommend',
    children: [
        {
            path: 'recommend',
            component: recommend
        },
        {
            path: 'songList',
            name: 'songList',
            component: songList
        },
        {
            path: 'newMusic',
            component: newMusic
        },
        {
            path: 'singer',
            component: singer
        }
    ]
}



