<template>
    <div id="app">
        <title-bar></title-bar>
        <main>
            <side-nav></side-nav>
            <section>
                <keep-alive>
                    <router-view/>
                </keep-alive>
            </section>
            <launch v-show="showLaunch"></launch>
        </main>
        <loading></loading>
    </div>
</template>

<script>
    import titleBar from '@/components/common/titleBar'
    import sideNav from '@/components/common/sideNav'
    import launch from '@/components/common/launch'
    import loading from '@/components/common/loading'

    export default {
        name: 'App',
        data() {
            return {
                showLaunch: true
            }
        },
        mounted() {
            setTimeout(() => {
                this.showLaunch = false;
            }, 1000);
        },
        components: {
            titleBar,
            sideNav,
            launch,
            loading
        }
    }
</script>

<style lang="scss">
    #app {
        position: relative;
        width: 1200px;
        height: 800px;
        background-color: #fff;
        color: #333;
        font-size: 14px;
        overflow: hidden;
        main {
            position: relative;
            height: calc(800px - 30px);
            display: flex;
            section {
                width: calc(1200px - 190px);
                padding: 0 20px 15px;
                overflow-y: auto;
            }
            .launchImg {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1000;
            }
        }
    }
</style>



