import Vue from 'vue'
import Router from 'vue-router'
import search from '@/router/search'
import find from '@/router/find'
import songListDetail from '@/router/songListDetail'
import favorites from '@/router/favorites'

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            redirect: '/find'
        },
        search,
        find,
        songListDetail,
        favorites
    ]
})



