<template>
    <div class="song_list">
        <column-header :icon="'icon-yinlewenjian'" :title="''" :link="'/'"></column-header>
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="item.id" v-if="index<10" @click="openWeb(item.id)">
                <i class="iconfont icon-copy"></i>
                <img :src="item.sPicUrl" alt="">
                <p class="title">{{item.name}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'

    export default {
        name: "songList",
        components: {
            columnHeader
        },
        data() {
            return {
                listData: []
            }
        },
        async mounted() {
            let res = await this.$axios.get('http://www.ruyiday.com/personalized/privatecontent');
            if (res.code === 200) {
                this.listData = res.result;
            }
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            }
        },
        methods: {
            openWeb(id) {
                window.open(`http://music.163.com/#/topic?id=${id}`);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .song_list {
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li {
                width: calc((100% - 18px * 2) / 3);
                margin-bottom: 24px;
                position: relative;
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                i {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                    z-index: 2;
                    padding: 6px;
                    border-radius: 50%;
                    color: #fff;
                    font-size: 14px;
                    background-color: rgba(0, 0, 0, 0.5);
                    box-shadow: 0 0 2px #fff;
                }
                img {
                    display: block;
                    width: 100%;
                }
                .title {
                    padding-top: 10px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
    }
</style>



