<template>
    <header>
        <span class="column_name">
            <i :class="`iconfont ${icon}`"></i>
            <span>{{title}}</span>
        </span>
        <router-link v-if="link.length" :to="link" tag="span" class="column_more"> ></router-link>
    </header>
</template>

<script>
    export default {
        name: "columnHeader",
        props: {
            icon: {
                type: String,
                default: ''
            },
            title: {
                type: String,
                default: ''
            },
            link: {
                type: String,
                default: ''
            }
        }
    }
</script>

<style lang="scss" scoped>
    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 10px;
        margin: 10px 0;
        border-bottom: 1px solid #ddd;
        .column_name {
            i {
                padding-right: 2px;
                vertical-align: text-top;
                color: #b7181a;
                font-size: 20px;
            }
        }
        .column_more {
            color: #999;
            font-size: 12px;
            cursor: default;
            &:hover{
                color: #bbb;
            }
        }
    }
</style>



