<template>
    <div class="find">
        <header>
            <div class="plate_header"></div>
            <div class="tab_colunm">
                <router-link to="/find/recommend" tag="span"></router-link>
                <router-link to="/find/songList" tag="span"></router-link>
                <router-link to="/find/newMusic" tag="span"></router-link>
                <router-link to="/find/singer" tag="span"></router-link>
            </div>
        </header>
        <div class="tab_content">
            <transition name="fade" mode="out-in">
                <keep-alive>
                    <router-view/>
                </keep-alive>
            </transition>
        </div>
    </div>
</template>

<script>
    export default {
        name: "find"
    }
</script>

<style lang="scss" scoped>
    .fade-enter-active, .fade-leave-active {
        transition: 0.2s;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
        transform: translateX(-20px);
    }

    .find {
        header {
            position: sticky;
            top: 0;
            z-index: 9;
            width: 100%;
            padding-top: 15px;
            background-color: #fff;
            .tab_colunm {
                padding-bottom: 10px;
                border-bottom: 1px solid #ddd;
                span {
                    margin-right: 16px;
                    padding-bottom: 7px;
                    border-bottom: 2px solid transparent;
                    cursor: default;
                    &.router-link-active {
                        color: #b7181a;
                        border-color: #b7181a;
                    }
                }
            }
        }
        .tab_content {
            padding-top: 20px;
        }
    }
</style>



