<template>
    <div class="mv_list">
        <column-header :icon="'icon-shipin1'" :title="'MV'" :link="'/'"></column-header>
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="item.id" v-if="index<3" @click="openWeb(item.id)">
                <span class="play_count">
                    <i class="iconfont icon-flyme_icon-"></i>
                    <span>{{item.playCount | unitMyriad}}</span>
                </span>
                <img :src="item.picUrl" alt="">
                <p class="title">{{item.name}}</p>
                <p class="artists">
                    <span v-for="artist in item.artists" :key="artist.id">{{artist.name}}</span>
                </p>
            </li>
        </ul>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'

    export default {
        name: "songList",
        components: {
            columnHeader
        },
        data() {
            return {
                listData: []
            }
        },
        async mounted() {
            let res = await this.$axios.get('http://www.ruyiday.com/personalized/mv');
            if (res.code === 200) {
                this.listData = res.result;
            }
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            }
        },
        methods: {
            openWeb(id) {
                window.open(`http://music.163.com/#/mv?id=${id}`);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .mv_list {
        overflow: hidden;
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li {
                width: calc((100% - 18px * 2) / 3);
                margin-bottom: 24px;
                position: relative;
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                .play_count {
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    padding-right: 4px;
                    color: #fff;
                    font-size: 12px;
                    background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
                    i {
                        display: inline-block;
                        vertical-align: text-top;
                        font-size: 18px;
                    }
                }
                img {
                    display: block;
                    width: 100%;
                }
                .title {
                    padding-top: 10px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .artists{
                    padding-top: 8px;
                    color: #999;
                    font-size: 12px;
                    span{
                        padding-right: 10px;
                    }
                }
            }
        }
    }
</style>



