<template>
    <div class="song_list">
        <ul v-if="listData.length">
            <router-link tag="li" v-for="item,index in listData" :key="index" :to="{path: '/songListDetail', query: {id: item.id}}">
                <div class="cover">
                    <img :src="item.coverImgUrl" alt="">
                    <span class="title">{{item.name}}</span>
                </div>
                <span style="width: 13%;">{{item.trackCount}}</span>
                <span style="width: 24%;">by {{item.creator.nickname}}</span>
                <span style="width: 13%;">{{item.playCount | unitMyriad}}</span>
            </router-link>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "songList",
        data() {
            return {
                listData: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            },
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    keywords: this.keyword,
                    type: 1000
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.playlists || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .song_list {
        ul {
            li {
                padding: 10px;
                display: flex;
                align-items: center;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                span {
                    height: 14px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .cover {
                    width: 50%;
                    display: flex;
                    align-items: center;
                    img {
                        width: 50px;
                        height: 50px;
                    }
                    span {
                        padding-left: 10px;
                    }
                }
                & > span {
                    color: #999;
                    font-size: 12px;
                }
            }
        }
    }
</style>



