<template>
    <div class="mv">
        <ul class="clearfix" v-if="listData.length">
            <li v-for="item,index in listData" :key="index">
                <span class="play_count">
                    <i class="iconfont icon-flyme_icon-"></i>
                    <span>{{item.playCount | unitMyriad}}</span>
                </span>
                <img :src="item.cover" alt="">
                <span class="play_duration">{{item.duration | formatSeconds}}</span>
                <p class="title">{{item.name}}</p>
                <p class="artists">
                    <span v-for="artist,artistIndex in item.artists" :key="artistIndex">{{artist.name}}</span>
                </p>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "mv",
        data() {
            return {
                listData: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        filters: {
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            },
            formatSeconds(a) {
                a = a / 1000;
                let hh = parseInt(a / 3600);
                if (hh < 10) hh = "0" + hh;
                let mm = parseInt((a - hh * 3600) / 60);
                if (mm < 10) mm = "0" + mm;
                let ss = parseInt((a - hh * 3600) % 60);
                if (ss < 10) ss = "0" + ss;
                let length = hh !== '00' ? hh + ":" + mm + ":" + ss : mm + ":" + ss;
                if (a > 0) {
                    return length;
                } else {
                    return "NaN";
                }
            }
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    type: 1004,
                    keywords: this.$store.state.searchKeyword
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.mvs || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .mv {
        ul {
            li {
                float: left;
                width: calc((100% - 8px * 4) / 5);
                height: 180px;
                margin-right: 8px;
                position: relative;
                &:nth-child(5n) {
                    margin-right: 0;
                }
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                .play_count {
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    padding-right: 4px;
                    color: #fff;
                    font-size: 12px;
                    background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
                    i {
                        display: inline-block;
                        vertical-align: text-top;
                        font-size: 18px;
                    }
                }
                img {
                    display: block;
                    width: 100%;
                    transition: opacity 0.2s;
                }
                .play_duration {
                    position: absolute;
                    top: 88px;
                    left: 4px;
                    z-index: 2;
                    padding-right: 4px;
                    color: #fff;
                    font-size: 12px;
                    background: linear-gradient(to right, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0));
                }
                .title {
                    padding-top: 10px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .artists {
                    padding-top: 8px;
                    color: #999;
                    font-size: 12px;
                    span {
                        padding-right: 10px;
                    }
                }
            }
        }
    }
</style>



