<template>
    <div class="song_list">
        <category :currTagName="currTagName" @tagChange="tagChangeHandle"></category>
        <ul v-if="songList.length" class="clearfix">
            <router-link tag="li" v-for="item in songList" :key="item.id" :to="{path: '/songListDetail', query: {id: item.id}}">
                <img :src="item.coverImgUrl" alt="">
                <p class="title">{{item.name}}</p>
                <p class="nickname">by {{item.creator.nickname}}</p>
            </router-link>
        </ul>
    </div>
</template>

<script>
    import category from '@/components/find/songList/category'

    export default {
        name: "songList",
        components: {
            category
        },
        data() {
            return {
                songList: [],
                currTagName: '',
                pageIndex: -1
            }
        },
        mounted() {
            document.getElementsByTagName('section')[0].addEventListener('scroll', this.scrollBottom);
            if (this.$route.params.tag) {
                this.tagChangeHandle(this.$route.params.tag);
            } else {
                this.getSongList();
            }
        },
        computed: {
            dataOffset() {
                return (this.pageIndex + 1) * 50;
            }
        },
        watch: {
            $route() {
                if (this.$route.params.tag) {
                    this.tagChangeHandle(this.$route.params.tag);
                }
            }
        },
        methods: {
            async getSongList() {
                let params = {
                    limit: 50,
                    offset: this.dataOffset,
                    cat: this.currTagName
                };
                let {code, playlists} = await this.$axios.get('http://www.ruyiday.com/top/playlist', {params});
                if (code === 200) {
                    this.songList = [...this.songList, ...playlists];
                }
            },
            tagChangeHandle(tagName) {
                this.currTagName = tagName;
                this.pageIndex = -1;
                this.songList = [];
                this.getSongList();
            },
            scrollBottom() {
                let list = document.getElementsByTagName('section')[0];
                let viewH = list.clientHeight;
                let contentH = list.scrollHeight;
                let scrollTop = list.scrollTop;
                if (scrollTop === contentH - viewH) {
                    this.pageIndex++;
                    this.getSongList();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .song_list {
        ul {
            padding-top: 26px;
            li {
                float: left;
                width: calc((100% - 20px * 5) / 6);
                height: 220px;
                margin-right: 20px;
                &:nth-child(6n) {
                    margin-right: 0;
                }
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                img {
                    width: 100%;
                    border: 1px solid #ddd;
                    transition: opacity 0.2s;
                }
                .title {
                    padding: 10px 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .nickname {
                    height: 16px;
                    color: #999;
                    font-size: 12px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
    }
</style>



