<template>
    <div class="new_music">
        <column-header :icon="'icon-cd'" :title="''" :link="'/find/newMusic'"></column-header>
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="item.id" v-if="index<10">
                <span class="index">{{index+1 | zeroize}}</span>
                <img :src="item.song.album.picUrl" alt="">
                <div class="music_info">
                    <p class="name">{{item.name}}<i class="iconfont icon-bofangmv mv_icon" v-show="item.song.mvid"></i></p>
                    <p class="artists">
                        <span v-for="artist in item.song.artists" :key="artist.id">{{artist.name}}</span>
                    </p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'

    export default {
        name: "newMusic",
        components: {
            columnHeader
        },
        data() {
            return {
                listData: []
            }
        },
        filters: {
            zeroize(num) {
                return num < 10 ? '0' + num : num;
            }
        },
        async mounted() {
            let res = await this.$axios.get('http://www.ruyiday.com/personalized/newsong');
            if (res.code === 200) {
                this.listData = res.result;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .new_music {
        ul {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 30px;
            li {
                width: 50%;
                padding: 5px 0;
                display: flex;
                align-items: center;
                border-right: 1px solid #ddd;
                &:nth-child(2n) {
                    border-right: none;
                }
                &:nth-child(4n+1), &:nth-child(4n-2) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #efefef;
                }
                .index {
                    padding: 0 10px;
                }
                img {
                    width: 50px;
                }
                .music_info {
                    padding-left: 10px;
                    .name {
                        line-height: 14px;
                        .mv_icon {
                            padding-left: 5px;
                            vertical-align: -4px;
                            color: #b7181a;
                            font-size: 30px;
                            text-shadow: 0 0 0 #b7181a;
                        }
                    }
                    .artists {
                        padding-top: 10px;
                        color: #999;
                        font-size: 12px;
                        span {
                            padding-right: 10px;
                        }
                    }
                }
            }
        }
    }
</style>



