<template>
    <div class="default">
        <div class="column search_hot">
            <header></header>
            <ul class="clearfix" v-if="hotSearchList.length">
                <li v-for="item,index in hotSearchList" :key="index" @click="toSearch(item.first)">{{item.first}}</li>
            </ul>
        </div>
        <div class="column search_history">
            <header>
                <span></span>
                <i class="iconfont icon-lajitong" style="color: #333;font-size: 18px;line-height: 14px;" @click="clearSearchHistory"></i>
            </header>
            <ul v-show="searchHistory.length">
                <li v-for="item,index in searchHistory" :key="index" @click="toSearch(item)">
                    <span>{{item}}</span>
                    <i class="iconfont icon-shanchu" style="color: #999;" @click.stop="deleteSearchHistory(index)"></i>
                </li>
            </ul>
            <p v-show="!searchHistory.length" style="padding-top: 40px;text-align: center;color: #999;font-size: 12px;"></p>
        </div>
    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex'

    export default {
        name: "default",
        data() {
            return {
                hotSearchList: []
            }
        },
        computed: mapState([
            'searchHistory'
        ]),
        mounted() {
            this.getHotSearch();
        },
        methods: {
            async getHotSearch() {
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search/hot');
                if (code === 200) {
                    this.hotSearchList = result.hots;
                }
            },
            toSearch(keyword) {
                this.setSearchKeyword(keyword);
                this.$emit('toSearch');
            },
            ...mapMutations([
                'setSearchKeyword',
                'deleteSearchHistory',
                'clearSearchHistory'
            ])
        }
    }
</script>

<style lang="scss" scoped>
    .default {
        padding-top: 30px;
        display: flex;
        justify-content: space-between;
        .column {
            width: calc((100% - 30px) / 2);
            header {
                padding-bottom: 10px;
                margin-bottom: 16px;
                color: #666;
                font-size: 15px;
                border-bottom: 1px solid #ddd;
            }
        }
        .search_hot {
            ul {
                li {
                    float: left;
                    margin-right: 8px;
                    margin-bottom: 16px;
                    padding: 4px 10px;
                    border: 1px solid #ddd;
                    border-radius: 14px;
                }
            }
        }
        .search_history {
            header {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            ul {
                max-height: 520px;
                overflow-y: auto;
                li {
                    padding-bottom: 22px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }
            }
        }
    }
</style>



