<template>
    <div class="search_header">
        <div class="input_box">
            <input type="text" placeholder="" :value="keyword" @input="inputChange" @keyup.enter="toSearch(keyword)">
            <i class="iconfont icon-sousuo search_icon" @click="toSearch(keyword)"></i>
            <i class="iconfont icon-shanchu empty_icon" v-show="keyword.length" @click="clearSearch"></i>
        </div>
        <ul class="suggest_list" v-show="showSuggest">
            <li style="color: #1465b4;" @click="toSearch(keyword)">{{keyword}}</li>
            <li v-for="item in suggestList" :key="" @click="toSearch(item.name)">
                <i class="iconfont icon-sousuo"></i>
                <span>{{item.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex'

    export default {
        name: "searchHeader",
        data() {
            return {
                showEmptyBtn: false,
                showSuggest: false,
                suggestList: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        mounted() {
            document.addEventListener('click', this.handleDocumentClick);
            document.addEventListener('touchstart', this.handleDocumentClick);
        },
        methods: {
            handleDocumentClick(e) {
                if (!this.$el.contains(e.target)) {
                    this.showSuggest = false;
                }
            },
            async inputChange(e) {
                let keyword = e.target.value;
                this.setSearchKeyword(keyword);
                if (keyword.length) {
                    let params = {
                        keywords: this.keyword
                    };
                    let {code, result} = await this.$axios.get('http://www.ruyiday.com/search/suggest', {params});
                    if (code === 200) {
                        this.showSuggest = true;
                        this.suggestList = result.songs || [];
                    }
                } else {
                    this.showSuggest = false;
                }
            },
            toSearch(keyword) {
                this.setSearchKeyword(keyword);
                this.addSearchHistory(keyword);
                this.showSuggest = false;
                this.$emit('toSearch');
            },
            clearSearch() {
                this.setSearchKeyword('');
                this.showSuggest = false;
            },
            ...mapMutations([
                'setSearchKeyword',
                'addSearchHistory'
            ])
        }
    }
</script>

<style lang="scss" scoped>
    .search_header {
        position: relative;
        .input_box {
            position: relative;
            input {
                box-sizing: border-box;
                width: 100%;
                height: 40px;
                padding: 0 58px 0 8px;
                font-size: 15px;
                outline: none;
                border: 1px solid #ddd;
                &::-webkit-input-placeholder {
                    color: #ccc;
                }
            }
            .search_icon {
                position: absolute;
                top: 12px;
                right: 10px;
                color: #ccc;
            }
            .empty_icon {
                position: absolute;
                top: 11px;
                right: 34px;
                color: #666;
                font-size: 18px;
            }
        }
        .suggest_list {
            position: absolute;
            top: 40px;
            left: 0;
            z-index: 10;
            width: 100%;
            background-color: #fff;
            border-left: 1px solid #ddd;
            border-right: 1px solid #ddd;
            li {
                padding: 0 8px;
                height: 40px;
                line-height: 40px;
                border-bottom: 1px solid #ddd;
                display: flex;
                align-items: center;
                &:hover {
                    background-color: #eee;
                }
                &:first-child:hover {
                    background-color: #fff;
                }
                i {
                    padding-right: 8px;
                    color: #ddd;
                    font-size: 13px;
                }
            }
        }
    }
</style>



