import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        loading: false,
        searchKeyword: '',
        searchHistory: [],
        collectSongListId: [],
        collectSongListTitle: [],
        favorites: []
    },
    mutations: {
        changeLoading(state, isLoading) {
            state.loading = isLoading;
        },
        setSearchKeyword(state, keyword) {
            state.searchKeyword = keyword;
        },
        addSearchHistory(state, keyword) {
            if (state.searchHistory.indexOf(keyword) < 0) {
                state.searchHistory.push(keyword);
            }
        },
        deleteSearchHistory(state, index) {
            state.searchHistory.splice(index, 1);
        },
        clearSearchHistory(state) {
            state.searchHistory = [];
        },
        switchCollectSongList(state, item) {
            if (state.collectSongListId.indexOf(item.id) >= 0) {
                const index = state.collectSongListId.indexOf(item.id);
                state.collectSongListId.splice(index, 1);
                state.collectSongListTitle.splice(index, 1);
            } else {
                state.collectSongListId.push(item.id);
                state.collectSongListTitle.push(item.title);
            }
        },
        switchFavorites(state, item) {
            let exist = false;
            if (state.favorites.length) {
                for (let i = 0; i < state.favorites.length; i++) {
                    if (state.favorites[i].id === item.id) {
                        exist = true;
                        state.favorites.splice(i, 1);
                    }
                }
            }
            if (!exist) {
                state.favorites.push(item);
            }
        }
    }
});

export default store;



