<template>
    <div class="song_list_detail" v-if="Object.keys(detail).length">
        <header>
            <div class="plate_header"></div>
            <div class="song_list_info">
                <div class="cover">
                    <span class="play_count">
                        <i class="iconfont icon-erjiicon"></i>
                        <span>{{detail.playCount | unitMyriad}}</span>
                    </span>
                    <img :src="detail.coverImgUrl" alt="">
                </div>
                <div class="text">
                    <p class="title">{{detail.name}}</p>
                    <p class="author">
                        <img :src="detail.creator.avatarUrl" alt="">
                        <span class="name">{{detail.creator.nickname}}</span>
                        <span class="time" style="color: #888">{{detail.createTime | timestampToTime}} </span>
                    </p>
                    <p class="option">
                        <span @click="switchCollectSongList({id: detail.id, title: detail.name})">
                            <i class="iconfont icon-add_folder"></i>
                            <span><span
                                v-show="this.collectSongListId.indexOf(detail.id)>=0"></span>({{detail.subscribedCount | unitMyriad}})</span>
                        </span>
                    </p>
                    <div class="tags">
                        <span></span>
                        <ul v-if="detail.tags.length" v-for="item,index in detail.tags" :key="index">
                            <router-link tag="li" :to="{name: 'songList', params: {tag: item}}">{{item}}</router-link>
                            <li class="division" v-if="!(index===detail.tags.length-1)">/</li>
                        </ul>
                    </div>
                    <p class="introduce">
                        <span style="color: #000;"></span>
                        <span>{{detail.description}}</span>
                    </p>
                </div>
            </div>
            <img :src="detail.coverImgUrl" alt="" class="bg">
        </header>

        <div class="cont">
            <div class="option">
                <i class="iconfont icon-bofang" style="color: #b7181a;"></i>
                <span></span>
            </div>
            <ul v-if="detail.tracks && detail.tracks.length">
                <li v-for="item,index in detail.tracks" :key="item.id" v-if="index<100">
                    <div class="main">
                        <span class="index">{{index+1 | zeroizeIndex}}</span>
                        <i :class="`iconfont ${isCollected(item.id)?'icon-xihuan1':'icon-xihuan'}`" @click="switchFavorites(item)"></i>
                        <span class="title">
                            <span>{{item.name}}</span>
                            <span class="alias" v-if="item.alia.length">( {{item.alia[0]}} )</span>
                        </span>
                        <i class="iconfont icon-bofangmv mv_icon" v-show="item.mv"></i>
                    </div>
                    <span class="artists">
                        <span v-for="artist,index in item.ar" :key="index">{{artist.name}}</span>
                    </span>
                    <span class="album">{{item.al.name}}</span>
                    <span class="duration">{{item.dt | formatSeconds}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import columnHeader from '@/components/common/columnHeader'
    import {mapState, mapMutations} from 'vuex'

    export default {
        name: "song_list_detail",
        components: {
            columnHeader
        },
        data() {
            return {
                detail: {}
            }
        },
        computed: mapState([
            'collectSongListId',
            'favorites'
        ]),
        filters: {
            timestampToTime(timestamp) {
                let date = new Date(timestamp);
                let Y = date.getFullYear() + '-';
                let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                let D = date.getDate() + ' ';
                return Y + M + D;
            },
            unitMyriad(num) {
                return num > 9999 ? Math.floor(num / 10000) + '' : num;
            },
            zeroizeIndex(num) {
                return num < 10 ? '0' + num : num;
            },
            formatSeconds(a) {
                a = a / 1000;
                let hh = parseInt(a / 3600);
                if (hh < 10) hh = "0" + hh;
                let mm = parseInt((a - hh * 3600) / 60);
                if (mm < 10) mm = "0" + mm;
                let ss = parseInt((a - hh * 3600) % 60);
                if (ss < 10) ss = "0" + ss;
                let length = hh !== '00' ? hh + ":" + mm + ":" + ss : mm + ":" + ss;
                if (a > 0) {
                    return length;
                } else {
                    return "NaN";
                }
            }
        },
        watch: {
            $route: 'getDetail'
        },
        mounted() {
            this.getDetail();
        },
        methods: {
            async getDetail() {
                let params = {
                    id: this.$route.query.id
                };
                let {code, playlist} = await this.$axios.get('http://www.ruyiday.com/playlist/detail', {params});
                if (code === 200) {
                    this.detail = playlist;
                }
            },
            ...mapMutations([
                'switchCollectSongList',
                'switchFavorites'
            ]),
            isCollected(id) {
                for (let i = 0; i < this.favorites.length; i++) {
                    if (this.favorites[i].id === id) {
                        return true;
                    }
                }
                return false;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .song_list_detail {
        padding-top: 15px;
        header {
            position: relative;
            height: 230px;
            .song_list_info {
                position: absolute;
                top: 28px;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 100%;
                display: flex;
                .cover {
                    position: relative;
                    .play_count {
                        position: absolute;
                        top: 0;
                        right: 0;
                        z-index: 2;
                        padding: 1px 4px 0 0;
                        color: #fff;
                        font-size: 12px;
                        background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
                        i {
                            display: inline-block;
                            vertical-align: middle;
                            font-size: 12px;
                            transform: scale(0.8);
                        }
                    }
                    img {
                        width: 200px;
                    }
                }
                .text {
                    padding-left: 20px;
                    font-size: 12px;
                    .title {
                        color: #000;
                        font-size: 20px;
                    }
                    .author {
                        padding: 15px 0 24px;
                        display: flex;
                        align-items: center;
                        img {
                            width: 30px;
                            height: 30px;
                            border-radius: 50%;
                        }
                        span {
                            padding-left: 10px;
                            font-size: 12px;
                        }
                    }
                    .option {
                        & > span {
                            padding: 5px 6px;
                            border: 1px solid #bbb;
                            background-color: rgba(255, 255, 255, 0.5);
                            i {
                                color: #999;
                                font-size: 18px;
                                vertical-align: -2px;
                            }
                        }
                    }
                    .tags {
                        padding: 20px 0;
                        display: flex;
                        color: #000;
                        ul {
                            display: flex;
                            li {
                                color: #1465b4;
                                &.division {
                                    padding: 0 3px;
                                    color: #999;
                                }
                            }
                        }
                    }
                    .introduce {
                        span {
                            color: #999;
                            line-height: 18px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            &:last-child {
                                display: inline-block;
                                width: 640px;
                                vertical-align: bottom;
                            }
                        }
                    }
                }
            }
            .bg {
                position: absolute;
                top: -50px;
                left: 0;
                width: 973px;
                height: 80%;
                opacity: 0.45;
                filter: blur(40px);
            }
        }
        .cont {
            .option {
                padding: 20px 0;
            }
            ul {
                li {
                    height: 46px;
                    padding: 6px 10px;
                    display: flex;
                    align-items: center;
                    &:nth-child(2n+1) {
                        background-color: #f8f8f8;
                    }
                    &:hover {
                        background-color: #f5f5f5;
                    }
                    .main {
                        width: 50%;
                        display: flex;
                        align-items: center;
                        .index {
                            padding-left: 10px;
                            text-align: center;
                            font-size: 12px;
                        }
                        i {
                            padding: 0 12px;
                            color: #999;
                            font-size: 14px;
                            &.icon-xihuan1 {
                                color: #b7181a;
                            }
                        }
                        .title {
                            height: 15px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            .alias {
                                color: #999;
                            }
                        }
                        .mv_icon {
                            padding-left: 5px;
                            padding-bottom: 4px;
                            color: #b7181a;
                            font-size: 30px;
                            text-shadow: 0 0 0 #b7181a;
                        }
                    }
                    .artists, .album, .duration {
                        color: #999;
                        font-size: 12px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                    .artists {
                        width: 22%;
                        padding-right: 20px;
                        span {
                            padding-right: 10px;
                        }
                    }
                    .album {
                        width: 20%;
                        padding-right: 20px;
                    }
                    .duration {
                        width: 8%;
                        text-align: right;
                    }
                }
            }
        }
    }
</style>



