<template>
    <swiper :options="swiperOption" @mouseover.native="showBannerBtn=true" @mouseout.native="showBannerBtn=false" class="banner">
        <swiper-slide v-for="banner,index in banners" :key="index" v-if="index!==banners.length-1">
            <img :src="banner.picUrl" alt="">
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-prev swiper-btn" slot="button-prev" v-show="showBannerBtn"></div>
        <div class="swiper-next swiper-btn" slot="button-next" v-show="showBannerBtn"></div>
    </swiper>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import {swiper, swiperSlide} from 'vue-awesome-swiper'

    export default {
        name: "banner",
        data() {
            return {
                swiperOption: {
                    initialSlide: 1,
                    loop: true,
                    observer: true,
                    observeParents: true,
                    // autoplay: {
                    //     delay: 10000,
                    //     disableOnInteraction: false,
                    // },
                    effect: 'coverflow',
                    slidesPerView: 2,
                    centeredSlides: true,
                    coverflowEffect: {
                        rotate: 0,
                        stretch: 10,
                        depth: 100,
                        modifier: 3
                    },
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true,
                    },
                    navigation: {
                        prevEl: '.swiper-prev',
                        nextEl: '.swiper-next'
                    }
                },
                showBannerBtn: false,
                banners: []
            }
        },
        components: {
            swiper,
            swiperSlide
        },
        async mounted() {
            let res = await this.$axios.get('http://www.ruyiday.com/banner');
            if (res.code === 200) {
                this.banners = res.banners;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .banner {
        position: relative;
        .swiper-slide {
            position: relative;
            &.swiper-slide-active {
                transform: translateY(-22px) !important;
            }
            &:hover:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                z-index: 2;
                width: 100%;
                height: 100%;
                background-color: rgba(255, 255, 255, 0.1);
            }
            img {
                display: block;
                width: 100%;
            }
        }
        .swiper-pagination-bullets {
            bottom: 28px;
        }
        .swiper-btn {
            position: absolute;
            top: 22px;
            z-index: 2;
            width: 36px;
            height: 175px;
            line-height: 175px;
            text-align: center;
            color: #fff;
            font-size: 50px;
            font-weight: lighter;
            cursor: default;
            &:hover {
                background-color: rgba(0, 0, 0, 0.2);
            }
            &.swiper-prev {
                left: 0;
            }
            &.swiper-next {
                right: 0;
            }
        }
    }
</style>



