<template>
    <div class="singer">
        <div class="filtrate">
            <div class="column">
                <span class="column_name"></span>
                <ul>
                    <li @click="currLanguage=100" :class="{active: currLanguage===100}"></li>
                    <li @click="currLanguage=200" :class="{active: currLanguage===200}"></li>
                    <li @click="currLanguage=600" :class="{active: currLanguage===600}"></li>
                    <li @click="currLanguage=700" :class="{active: currLanguage===700}"></li>
                    <li @click="currLanguage=400" :class="{active: currLanguage===400}"></li>
                </ul>
            </div>
            <div class="column">
                <span class="column_name"></span>
                <ul>
                    <li @click="currClassify=1" :class="{active: currClassify===1}"></li>
                    <li @click="currClassify=2" :class="{active: currClassify===2}"></li>
                    <li @click="currClassify=3" :class="{active: currClassify===3}"></li>
                </ul>
            </div>
        </div>
        <ul class="singer_list clearfix" v-if="listData.length">
            <li v-for="item in listData" :key="item.id">
                <img :src="item.img1v1Url" alt="">
                <p>
                    <span>{{item.name}}</span>
                    <i class="iconfont icon-yonghu" v-if="item.accountId"></i>
                </p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "singer",
        data() {
            return {
                currLanguage: 100,
                currClassify: 1,
                listData: [],
                pageIndex: -1
            }
        },
        computed: {
            dataOffset() {
                return (this.pageIndex + 1) * 50;
            }
        },
        watch: {
            currLanguage() {
                this.pageIndex = -1;
                this.listData = [];
                this.getSingerList();
            },
            currClassify() {
                this.pageIndex = -1;
                this.listData = [];
                this.getSingerList();
            }
        },
        mounted() {
            document.getElementsByTagName('section')[0].addEventListener('scroll', this.scrollBottom);
            this.getSingerList();
        },
        methods: {
            async getSingerList() {
                let params = {
                    limit: 50,
                    offset: this.dataOffset,
                    cat: `${this.currLanguage}${this.currClassify}`
                };
                let {code, artists} = await this.$axios.get('http://www.ruyiday.com/artist/list', {params});
                if (code === 200) {
                    this.listData = [...this.listData, ...artists];
                }
            },
            scrollBottom() {
                let list = document.getElementsByTagName('section')[0];
                let viewH = list.clientHeight;
                let contentH = list.scrollHeight;
                let scrollTop = list.scrollTop;
                if (scrollTop === contentH - viewH) {
                    this.pageIndex++;
                    this.getSingerList();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .singer {
        .filtrate {
            font-size: 12px;
            .column {
                display: flex;
                .column_name {
                    color: #666;
                }
                ul {
                    padding-bottom: 16px;
                    display: flex;
                    color: #999;
                    li {
                        padding: 0 10px;
                        border-right: 1px solid #ddd;
                        &:last-child {
                            border-right: none;
                        }
                        &.active {
                            color: #b7181a;
                        }
                    }
                }
            }
        }
        .singer_list {
            padding-top: 10px;
            li {
                float: left;
                width: calc((100% - 20px * 5) / 6);
                height: 194px;
                margin-right: 20px;
                &:nth-child(6n) {
                    margin-right: 0;
                }
                &:hover {
                    img {
                        opacity: 0.8;
                    }
                }
                img {
                    display: block;
                    width: 100%;
                    border: 1px solid #eee;
                    transition: opacity 0.2s;
                }
                p {
                    padding-top: 10px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    span{
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                    i{
                        color: #b7181a;
                        font-size: 15px;
                    }
                }
            }
        }
    }
</style>



