<template>
    <div class="album">
        <ul v-if="listData.length">
            <router-link tag="li" v-for="item,index in listData" :key="index" :to="{path: '/songListDetail', query: {id: item.id}}">
                <div class="main">
                    <div class="cover">
                        <img :src="item.picUrl" alt="">
                    </div>
                    <span class="title">{{item.name}}</span>
                    <span v-if="item.alias.length" style="color: #999">{{item.alias[0]}}</span>
                </div>
                <div class="artist">
                    <span>{{item.artist.name}}</span>
                    <span v-if="item.artist.alias.length" style="color: #999">{{item.artist.alias[0]}}</span>
                </div>
            </router-link>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "album",
        data() {
            return {
                listData: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    type: 10,
                    keywords: this.$store.state.searchKeyword
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.albums || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .album {
        ul {
            li {
                padding: 10px;
                display: flex;
                align-items: center;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                .main {
                    width: 70%;
                    display: flex;
                    align-items: center;
                    .cover {
                        width: 52px;
                        height: 50px;
                        position: relative;
                        &:after {
                            content: '';
                            position: absolute;
                            top: 3px;
                            left: 15px;
                            width: 44px;
                            height: 44px;
                            background-color: #333;
                            border-radius: 50%;
                        }
                        img {
                            position: absolute;
                            top: 0;
                            left: 0;
                            z-index: 1;
                            width: 100%;
                            height: 100%;
                            border-right: 2px solid rgba(255, 255, 255, 0.2);
                        }
                    }
                    .title {
                        padding-left: 20px;
                    }
                }
                .artist {
                    width: 30%;
                    color: #999;
                    font-size: 12px;
                }
            }
        }
    }
</style>



