<template>
    <nav class="side_nav">
        <div class="menu_btn">
            <i class="iconfont icon-menu"></i>
        </div>
        <ul class="menu_link">
            <router-link to="/search" tag="li">
                <i class="iconfont icon-sousuo"></i>
                <span></span>
            </router-link>
            <router-link to="/find" tag="li">
                <i class="iconfont icon-yinle"></i>
                <span></span>
            </router-link>
        </ul>
        <p class="colunm">
            <span></span>
        </p>
        <ul class="menu_link">
            <router-link tag="li" to="/favorites">
                <i class="iconfont icon-xihuan"></i>
                <span></span>
            </router-link>
        </ul>
        <p class="colunm">
            <span></span>
        </p>
        <ul v-show="collectSongListId.length" class="collect_link">
            <router-link tag="li" v-for="item,index in collectSongListTitle" :key="index"
                         :to="{path: '/songListDetail', query: {id: collectSongListId[index]}}">
                <i class="iconfont icon-yinpingedan"></i>
                <span>{{item}}</span>
            </router-link>
        </ul>
    </nav>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "sideNav",
        computed: mapState([
            'collectSongListId',
            'collectSongListTitle'
        ])
    }
</script>

<style lang="scss" scoped>
    .side_nav {
        width: 190px;
        height: 100%;
        border-right: 1px solid #ddd;
        background-color: #f5f5f5;
        font-size: 13px;
        .menu_btn {
            color: black;
            padding: 10px;
        }
        ul {
            margin-bottom: 20px;
            li {
                display: flex;
                align-items: center;
                border-left: 2px solid transparent;
                padding: 10px 10px 10px 8px;
                &:hover {
                    background-color: #e8e8e8;
                }
                .iconfont {
                    padding-top: 2px;
                    padding-right: 10px;
                    color: #666;
                    font-size: 14px;
                }
                span {
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
        .colunm {
            padding: 10px;
            color: #999;
            font-size: 12px;
        }
        .menu_link {
            li.router-link-active {
                background-color: #ddd;
                border-color: #b7181a;
                .iconfont {
                    color: #333;
                }
            }
        }
        .collect_link {
            li.router-link-exact-active {
                background-color: #ddd;
                border-color: #b7181a;
                .iconfont {
                    color: #333;
                }
            }
        }
    }
</style>



