<template>
    <div class="category">
        <div class="all_category">
            <div class="curr_category" @click="showDetailCategory=!showDetailCategory">{{currTagName}}</div>
            <div class="arrows_icon" v-show="showDetailCategory">^</div>
            <div class="category_list" v-show="showDetailCategory">
                <div class="all">
                    <p :class="{tag_active: currTagName===''}" @click="tagChange('')"></p>
                </div>
                <div class="list">
                    <div class="column" v-for="value,key in categoriesData.categories" :key="key">
                        <div class="column_name">
                            <i :class="`iconfont ${category_icons[key]}`"></i>
                            <span>{{value}}</span>
                        </div>
                        <ul class="column_cont_list">
                            <li v-for="item,index in categoriesData.sub" :key="index" v-if="item.category===Number(key)"
                                @click="tagChange(item.name)" :class="{tag_active: currTagName===item.name, hot: item.hot}">
                                <span>{{item.name}}</span>
                                <span class="hot_flag" v-show="item.hot">HOT</span>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <ul class="hot_category" v-if="hotCategories.length">
            <li v-for="item,index in hotCategories" :key="index" v-if="index<8" @click="tagChange(item.name)">{{item.name}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "category",
        data() {
            return {
                categoriesData: {},
                hotCategories: [],
                category_icons: ['icon-earth', 'icon-gangqin', 'icon-kafei', 'icon-smile', 'icon-zhuti'],
                showDetailCategory: false
            }
        },
        props: {
            currTagName: {
                type: String,
                default: ''
            }
        },
        async mounted() {
            document.addEventListener('click', this.handleDocumentClick);
            document.addEventListener('touchstart', this.handleDocumentClick);
            this.categoriesData = await this.$axios.get('http://www.ruyiday.com/playlist/catlist');
            let res = await this.$axios.get('http://www.ruyiday.com/playlist/hot');
            this.hotCategories = res.tags || [];
        },
        methods: {
            handleDocumentClick(e) {
                if (!this.$el.contains(e.target)) {
                    this.showDetailCategory = false;
                }
            },
            tagChange(tagName) {
                this.showDetailCategory = false;
                this.$emit('tagChange', tagName);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .category {
        display: flex;
        align-items: center;
        font-size: 12px;
        .all_category {
            position: relative;
            .curr_category {
                padding: 4px 8px;
                border: 1px solid #ddd;
                border-radius: 2px;
                cursor: default;
                &:after {
                    content: '';
                    display: inline-block;
                    padding-left: 3px;
                    color: #666;
                    transform: scale(1.2, 0.6);
                }
            }
            .arrows_icon {
                position: absolute;
                top: 28px;
                left: 28px;
                z-index: 2;
                height: 15px;
                display: inline-block;
                color: #ddd;
                font-size: 30px;
                font-weight: lighter;
                transform: scaleX(1.6);
                background-color: #fff;
            }
            .category_list {
                position: absolute;
                top: 42px;
                left: 0;
                width: 660px;
                height: 390px;
                color: #666;
                background-color: #fff;
                border: 1px solid #ccc;
                overflow-y: auto;
                .tag_active {
                    border-color: #b7181a !important;
                    &:before {
                        content: '';
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        width: 0;
                        height: 0;
                        border-left: 16px solid transparent;
                        border-bottom: 16px solid #b7181a;
                    }
                    &:after {
                        content: '';
                        position: absolute;
                        right: 0;
                        bottom: 1px;
                        z-index: 9;
                        height: 10px;
                        line-height: 10px;
                        color: #fff;
                        font-size: 12px;
                    }
                }
                .all {
                    padding: 20px;
                    border-bottom: 1px solid #ddd;
                    p {
                        width: 250px;
                        height: 30px;
                        line-height: 30px;
                        text-align: center;
                        border: 1px solid #ddd;
                        position: relative;
                    }
                }
                .list {
                    padding: 10px 20px 0;
                    .column {
                        display: flex;
                        align-items: start;
                        margin-bottom: 10px;
                        &:last-child {
                            margin-bottom: 0;
                        }
                        .column_name {
                            width: 12%;
                            color: #e65b5d;
                            i {
                                padding-right: 5px;
                                font-size: 26px;
                                vertical-align: -5px;
                            }
                        }
                        .column_cont_list {
                            width: 88%;
                            display: flex;
                            flex-wrap: wrap;
                            li {
                                margin-left: 8px;
                                margin-bottom: 8px;
                                width: 80px;
                                height: 30px;
                                line-height: 30px;
                                text-align: center;
                                border: 1px solid #ddd;
                                position: relative;
                                .hot_flag {
                                    position: absolute;
                                    top: -11px;
                                    right: -3px;
                                    color: #e65b5d;
                                    transform: scale(0.6);
                                }
                            }
                        }
                    }
                }
            }
        }
        .hot_category {
            display: flex;
            align-items: center;
            color: #666;
            line-height: normal;
            li {
                padding: 0 20px;
                border-right: 1px solid #ddd;
                cursor: default;
                &:last-child {
                    border-right: none;
                }
            }
        }
    }
</style>



