<template>
    <div class="singer">
        <ul v-if="listData.length">
            <li v-for="item,index in listData" :key="index">
                <img :src="item.img1v1Url" alt="">
                <span>{{item.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "singer",
        data() {
            return {
                listData: []
            }
        },
        computed: {
            ...mapState({
                keyword: state => state.searchKeyword
            })
        },
        watch: {
            keyword() {
                this.getListData();
            },
            $route: 'getListData'
        },
        mounted() {
            this.getListData();
        },
        methods: {
            async getListData() {
                let params = {
                    limit: 100,
                    type: 100,
                    keywords: this.$store.state.searchKeyword
                };
                let {code, result} = await this.$axios.get('http://www.ruyiday.com/search', {params});
                if (code === 200) {
                    this.listData = result.artists || [];
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .singer {
        ul {
            li {
                padding: 10px;
                display: flex;
                align-items: center;
                &:nth-child(2n+1) {
                    background-color: #f8f8f8;
                }
                &:hover {
                    background-color: #f5f5f5;
                }
                img {
                    width: 50px;
                    height: 50px;
                }
                span {
                    padding-left: 10px;
                }
            }
        }
    }
</style>



