import search from '@/pages/search/index'
import searchDefault from '@/pages/search/default'
import searchResult from '@/pages/search/result/index'
import searchSingle from '@/pages/search/result/single'
import searchSinger from '@/pages/search/result/singer'
import searchAlbum from '@/pages/search/result/album'
import searchMV from '@/pages/search/result/mv'
import songList from '@/pages/search/result/songList'
import user from '@/pages/search/result/user'

export default {
    path: '/search',
    component: search,
    children: [
        {
            path: '',
            component: searchDefault
        },
        {
            path: 'result',
            component: searchResult,
            children: [
                {
                    path: 'single',
                    component: searchSingle
                },
                {
                    path: 'singer',
                    component: searchSinger
                },
                {
                    path: 'album',
                    component: searchAlbum
                },
                {
                    path: 'mv',
                    component: searchMV
                },
                {
                    path: 'songList',
                    component: songList
                },
                {
                    path: 'user',
                    component: user
                }
            ]
        }
    ]
}



